import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';

import './spinner.css';




const MEDIA_SIZES = {
  xxs: '12',
  xs: '16',
  s: '24',
  m: '36',
  l: '48',
  xl: '72',
  xxl: '120',
};

// const Gradient;
export const LOADING_CLASS = 'spinner';

const Spinner = ({ color, size, inverted, ...other }) => {
  return (
    <div style={{
      position: 'relative',
      display: 'block'
    }}>
      <svg
        aria-labelledby="title"
        role="progressbar"
        viewBox="0 0 50 50"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
        className="spinner--svg"
        size={size}
        height={size}
        width={size}
      >
        <title id="title" className="visibility--a11yHide">
          Loading
        </title>
        {inverted ? null : (
          <defs>
            <linearGradient id="LoadingSpinnerGradient">
              <stop offset="0%" stopColor='#3498db' stopOpacity=".9" />
              <stop offset="20%" stopColor='#3498db' stopOpacity=".8" />
              <stop offset="60%" stopColor='#3498db' stopOpacity=".7" />
              <stop offset="90%" stopColor='#3498db' stopOpacity=".6" />
            </linearGradient>
          </defs>
        )}

        <circle
          cx="25"
          cy="25"
          r="22"
          stroke={inverted ? 'rgba(255,255,255,.6)' : 'rgba(52,152,219, .1)'}
          strokeWidth="6"
        />
        <circle
          className="spinner--dash"
          cx="25"
          cy="25"
          r="22"
          stroke={inverted ? '#fff' : 'url(#LoadingSpinnerGradient)'}
          strokeWidth="6"
        />
      </svg>
    </div>
  );
};

Spinner.propTypes = {
  /**
   * Composes the Box component as the base.
   */
  /**
   *  Delay after which spinner should be visible.
   */
  delay: PropTypes.number,
  size: PropTypes.number.isRequired
};

Spinner.defaultProps = {
  color: '',
  scrimColor: 'rgba(255,255,255, .87)',
  size: MEDIA_SIZES.l
};

export default Spinner;
