import React, { Component } from 'react';
import { theme, mixins, media, Section, A, H3, P, Ul } from '../../styles';
import styled from 'styled-components';

import { email } from '../../config';
import {
  IconDribbble,
  IconGithub,
  IconLinkedin,
  IconCodeSandbox,
  IconTwitter,
  IconBehance,
} from '../icons';

import { socialMedia } from '../../config';

const SIZEICON = '22px'

const SocialLink = styled(A)`
  padding: 16px;
  z-index: 10;
  svg {
    width: ${SIZEICON};
    height: ${SIZEICON};
  }
`;

const Footer = styled.footer`
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 120px;
`;

const EmailButton = styled.div``;

const EmailLink = styled(A)`
  ${mixins.bigButton};
  font-size: ${theme.fontSizes.medium};
  margin-top: 16px;
`;


const SocialItem = styled.li`
  &:last-of-type {
    margin-bottom: 0px;
  }
`;
const SocialItemList = styled(Ul)`
  margin-top: 20px;
  display: inline-flex;
  align-items: center;
`;

export default () => {
  return (
    <React.Fragment>
      <Footer>
        <EmailButton>
          <EmailLink href={`mailto:${email}`}>Get In Touch</EmailLink>
        </EmailButton>
        <SocialItemList>
          {
            socialMedia.map(({ url, name }, i) => (
              <SocialItem key={i}>
                <SocialLink
                  href={url}
                  target="_blank"
                  rel="nofollow noopener noreferrer"
                  aria-label={name}>
                  {name === 'Dribbble' ? (
                    <IconDribbble />
                    ) : name === 'Linkedin' ? (
                      <IconLinkedin />
                  ) : name === 'Behance' ? (
                    <IconBehance />
                  ) : name === 'CodeSandbox' ? (
                    <IconCodeSandbox />
                  ) : name === 'Twitter' ? (
                    <IconTwitter />
                  ) : (
                    <IconGithub />
                  )}
                </SocialLink>
              </SocialItem>
            ))}
        </SocialItemList>
      </Footer>
    </React.Fragment>
  );
};
