import React, { Component, Children } from 'react';
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css'; // This only needs to be imported once in your app
import styled from 'styled-components';
import { theme, mixins, media, Section, A, H3, P, Ul } from '../../styles';

import A0 from '../../images/a0.jpg';
import A1 from '../../images/a1.png';
import A2 from '../../images/MobileSpike.jpg';
import A3 from '../../images/navA10.jpg';
import A4 from '../../images/navA10Prot.jpg';
import A5 from '../../images/DS.jpg';

const images = [A1, A0, A3, A4, A2, A5];

const ALink = styled.div`

  ${mixins.smallButton};
  font-size: ${theme.fontSizes.medium};
  /* &:hover{
      border-radius: 6px;
      box-shadow: 0 36px 64px 0 rgba(0, 0, 0, 0.1), 0 6px 30px 0 rgba(0, 0, 0, 0.05);
    } */
`;

export default class Lightboxing extends Component {
  constructor(props) {
    super(props);

    this.state = {
      photoIndex: 0,
      isOpen: false,
    };
  }

  render() {
    const { photoIndex, isOpen } = this.state;
    
    return (
      <div>
        <div style={{
          display: 'flex',
          justifyContent: 'center'
        }}>
          <ALink type="button" onClick={() => this.setState({ isOpen: true })}>
            Click here to see more
          </ALink>
        </div>

        {isOpen && (
          <Lightbox
            mainSrc={images[photoIndex]}
            nextSrc={images[(photoIndex + 1) % images.length]}
            prevSrc={images[(photoIndex + images.length - 1) % images.length]}
            onCloseRequest={() => this.setState({ isOpen: false })}
            onMovePrevRequest={() =>
              this.setState({
                photoIndex: (photoIndex + images.length - 1) % images.length,
              })
            }
            onMoveNextRequest={() =>
              this.setState({
                photoIndex: (photoIndex + 1) % images.length,
              })
            }
          />
        )}
      </div>
    );
  }
}
